* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background: #1a1a2e;
  color: #eee;
}

.app-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
}

.header-section {
  background: #16213e;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 20px;
}

.header-section h1 {
  margin: 0 0 15px 0;
  color: #e94560;
}

.header-info p {
  margin: 5px 0;
  font-size: 14px;
  word-break: break-all;
}

.section {
  background: #16213e;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 20px;
}

.section h2 {
  margin: 0 0 15px 0;
  color: #e94560;
  border-bottom: 1px solid #e94560;
  padding-bottom: 10px;
}

.form-group {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 10px;
}

.form-group input {
  flex: 1;
  min-width: 150px;
  padding: 10px;
  border: 1px solid #0f3460;
  border-radius: 4px;
  background: #1a1a2e;
  color: #eee;
}

.form-group input:focus {
  outline: none;
  border-color: #e94560;
}

button {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  background: #e94560;
  color: white;
  cursor: pointer;
  font-weight: bold;
}

button:hover:not(:disabled) {
  background: #ff6b6b;
}

button:disabled {
  background: #555;
  cursor: not-allowed;
  opacity: 0.6;
}

.auction-card {
  background: #0f3460;
  padding: 15px;
  border-radius: 6px;
  margin-bottom: 15px;
}

.auction-card h4 {
  margin: 0 0 10px 0;
  color: #e94560;
}

.auction-card p {
  margin: 5px 0;
  font-size: 14px;
  word-break: break-all;
}

.auction-card.pending-fulfill {
  border-left: 4px solid #ffc107;
}

.auction-card.fulfilled {
  border-left: 4px solid #28a745;
}

.auction-card.canceled {
  border-left: 4px solid #dc3545;
}

.auction-actions {
  display: flex;
  gap: 10px;
  margin-top: 10px;
  flex-wrap: wrap;
}

.auction-actions input {
  padding: 8px;
  border: 1px solid #0f3460;
  border-radius: 4px;
  background: #1a1a2e;
  color: #eee;
  width: 120px;
}

.highlight {
  color: #28a745;
  font-weight: bold;
}

.claim-section {
  margin-top: 20px;
  padding-top: 15px;
  border-top: 1px solid #0f3460;
}

.owner-section {
  border: 2px solid #e94560;
}

.owner-controls {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.destroy-btn {
  background: #dc3545;
}

.destroy-btn:hover:not(:disabled) {
  background: #c82333;
}
